import React from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { makeStyles } from "@material-ui/styles";
import study1 from "../assets/lucas.png";
import study2 from "../assets/RDJ.png";
import study3 from "../assets/student.png";
import study5 from "../assets/einstien.png";

const userStyles = makeStyles(theme => ({
  container: {
    width: "auto",
    padding: 50,
    margin: "auto",
    backgroundColor: theme.palette.primary.main
  },
  slider: {
    margin: "auto"
  },
  img: {
    width: "auto"
  }
}));

const Home = () => {
  var settings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 2,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 3000,
    arrows: false
  };
  const classes = userStyles();
  const list = [study1, study2, study3, study5].map((study, index) => {
    return (
      <div key={index} className={classes.container}>
        <img className={classes.img} src={study} alt={`study${index}`} />
      </div>
    );
  });
  return (
    <div>
      <h2>Home</h2>
      <Slider className={classes.slider} {...settings}>
        {list}
      </Slider>
    </div>
  );
};

export default Home;
